<template>
  <div>
    <div style="padding: 0 100px">
      <form v-show="!newButton" class="modify" @submit.prevent="modifyLetter">
        <input type="text" placeholder=".." />
        <input type="text" placeholder=".." />
        <input type="file" placeholder=".." />
        <input type="submit" value="" />
      </form>
      <form v-show="newButton" class="new" @submit.prevent="newLetter">
        <div> </div>
        <input v-model="title" type="text" placeholder="......" />
        <input v-model="text" type="text" placeholder="......" />
        <input
          v-on:change="fileSelect"
          ref="image"
          type="file"
          placeholder="......"
          accept=".jpg, .jpeg"
        />
        <input type="submit" value="" />
      </form>
    </div>
    <hr />

    <div style="padding: 0 100px">
      <button @click="handleNewButtonClick"></button>
      <table class="table table-hover">
        <thead>
          <tr>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
          </tr>
        </thead>
        <tbody>
          <tr class="table-active">
            <th>1</th>
            <td></td>
            <td>  </td>
            <td>133</td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="text-center">
      <a href="#"></a>
      <a href="#" style="margin: 0 5px">1</a>
      <a href="#"></a>
    </div>
  </div>
</template>

<script>
export default {
  name: "NewsLetter",
  data() {
    return {
      newButton: false,
      title: "",
      text: "",
      image: "",
    };
  },
  methods: {
    fileSelect() {
      this.image = this.$refs.image.files[0];
    },
    handleNewButtonClick() {
      this.newButton = !this.newButton;
    },
    newLetter() {
      if (this.title === "" || this.text === "" || this.image === "") {
        console.log(this.title, this.text, this.image);
        alert("");
        return;
      }

      const formData = new FormData();
      formData.append("title", this.title);
      formData.append("text", this.text);
      formData.append("image", this.image);

      let data = {
        url: "news",
        method: "post",
        formData,
      };
      this.$store.dispatch("news", data);
    },
    modifyLetter() {},
  },
};
</script>

<style scoped></style>



