<script>
import store from "../../vuex/store";
import CryptoJS from "crypto-js";
import { getNames } from "country-list";
import Navbar from "../Navbar";

export default {
  name: "account-signup",
  components: { Navbar },
  data() {
    return {
      email: "",
      password: "",
      passwordConfim: "",
      country: "",
      name: "",
      position: "",
      phone: "",
      company: "",
      address: "",
      checked: [],
      validationPassword: " //  2  ",
      validationReEnterPassword: "",
      validationPhone: "ex) 123-4567-8910",
      validationAddress: "",
    };
  },
  computed: {
    countryList: function () {
      return getNames();
    },
  },
  methods: {
    moveTo(location) {
      this.$router.push(location);
    },
    checkPhone() {
      let hp = this.phone;
      if (/^[0-9]{2,3}-[0-9]{3,4}-[0-9]{4}/.test(hp)) {
        this.validationPhone = "";
        return true;
      }
      this.validationPhone = "ex) 123-4567-8910";
      return false;
    },
    checkReEnterPassword() {
      if (this.password !== this.passwordConfim) {
        this.validationReEnterPassword = "  .";
        return false;
      }
      this.validationReEnterPassword = "";
      return true;
    },
    checkPassword() {
      let pw = this.password;
      let num = pw.search(/[0-9]/g);
      let eng = pw.search(/[a-z]/gi);
      let spe = pw.search(/[`~!@@#$%^&*|'";:/?]/gi);

      if (pw.search(/\s/) != -1) {
        this.validationPassword = "   .";
        this.checkReEnterPassword();
        return false;
      } else if (
        (num < 0 && eng < 0) ||
        (eng < 0 && spe < 0) ||
        (spe < 0 && num < 0)
      ) {
        this.validationPassword =
          " //  2  ";
        this.checkReEnterPassword();
        return false;
      } else if (pw.length < 10 || pw.length > 20) {
        this.checkReEnterPassword();
        this.validationPassword = "10 ~ 20  .";
        return false;
      } else {
        this.checkReEnterPassword();
        this.validationPassword = "";
        return true;
      }
    },
    validationCheckAll(index) {
      const allCheckBox = document.querySelector(".check-box");
      const allMobilecheckBox = document.querySelector(".mobile-check-box");
      const checkBoxes = document.querySelectorAll(".children-box");

      if (index === 0) {
        if (checkBoxes[0].checked) {
          checkBoxes[3].checked = true;
        } else {
          checkBoxes[3].checked = false;
        }
      } else if (index === 1) {
        if (checkBoxes[1].checked) {
          checkBoxes[4].checked = true;
        } else {
          checkBoxes[4].checked = false;
        }
      } else if (index === 2) {
        if (checkBoxes[2].checked) {
          checkBoxes[5].checked = true;
        } else {
          checkBoxes[5].checked = false;
        }
      } else if (index === 3) {
        if (checkBoxes[3].checked) {
          checkBoxes[0].checked = true;
        } else {
          checkBoxes[0].checked = false;
        }
      } else if (index === 4) {
        if (checkBoxes[4].checked) {
          checkBoxes[1].checked = true;
        } else {
          checkBoxes[1].checked = false;
        }
      } else if (index === 5) {
        if (checkBoxes[5].checked) {
          checkBoxes[2].checked = true;
        } else {
          checkBoxes[2].checked = false;
        }
      }

      for (let i = 0; i < checkBoxes.length; i++) {
        if (!checkBoxes[i].checked) {
          allCheckBox.checked = false;
          allMobilecheckBox.checked = false;
          return;
        }
      }
      allCheckBox.checked = true;
      allMobilecheckBox.checked = true;
    },
    checkAll(index) {
      const allCheckBox = document.querySelector(".check-box");
      const allMobilecheckBox = document.querySelector(".mobile-check-box");
      const checkBoxes = document.querySelectorAll(".children-box");

      if (index === 1) {
        if (allCheckBox.checked) {
          allMobilecheckBox.checked = true;
          for (let i = 0; i < checkBoxes.length; i++) {
            checkBoxes[i].checked = true;
          }
        } else {
          allMobilecheckBox.checked = false;
          for (let i = 0; i < checkBoxes.length; i++) {
            checkBoxes[i].checked = false;
          }
        }
      } else {
        if (allMobilecheckBox.checked) {
          allCheckBox.checked = true;
          for (let i = 0; i < checkBoxes.length; i++) {
            checkBoxes[i].checked = true;
          }
        } else {
          allCheckBox.checked = false;
          for (let i = 0; i < checkBoxes.length; i++) {
            checkBoxes[i].checked = false;
          }
        }
      }
    },
    join() {
      const checkBoxes = document.querySelectorAll(".children-box");

      if (
        this.email === "" ||
        this.password === "" ||
        this.passwordConfim === "" ||
        this.name === "" ||
        this.country === "" ||
        this.position === "" ||
        this.phone === "" ||
        this.company === "" ||
        this.address === "" ||
        !this.checkPhone()
      ) {
        alert("  ");
        return;
      }
      if (!this.checkReEnterPassword()) {
        return;
      }

      for (let i = 0; i < checkBoxes.length; i++) {
        if (!checkBoxes[i].checked) {
          alert(" ");
          return;
        }
      }
      let encryptPassword = CryptoJS.AES.encrypt(
        this.password,
        "saseul"
      ).toString();

      const data = {
        url: "signup",
        email: this.email,
        password: encryptPassword,
        name: this.name,
        country: this.country,
        position: this.position,
        phone: this.phone,
        company: this.company,
        address: this.address,
      };
      store.dispatch("account", data);
    },
  },
};
</script>

